@import url('https://fonts.googleapis.com/css?family=Muli:400,500,600,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,600,700&display=swap');
$font-stack: 'Muli', -apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", sans-serif;
$fa-font-path: '../fonts/';

@import "~bootstrap/scss/bootstrap";
@import "~@fortawesome/fontawesome-free/scss/fontawesome";
@import "~bootstrap-select/sass/bootstrap-select.scss";
@import "~@fortawesome/fontawesome-free/scss/solid.scss";
@import './transitions.scss';

.mw-110 {
    max-width: 110px;
}

.mw-80 {
    min-width: 80px;
}

.mw-60 {
    min-width: 60px;
}

html {
    height: 100%;
}
body {
    color: #292C49 !important;
    background: #F7F7FB;
    height: 100%;
    line-height: 150%;
    font-family: $font-stack;
    
    .login-container {
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        &__background {
            background-color: #ececec;
            // background: linear-gradient(0deg, rgba(141, 200, 153, 0.8), rgba(141, 200, 153, 0.8)), url('../img/register-cover-mobile.jpg');
            background: url('../img/register-cover-mobile.jpg') #7eb288;
            background-repeat: no-repeat;
            background-size: auto 100%;
            background-position: center;
            position: fixed;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
        }
        .login-content {
            
            &__logo {
                padding: 50px 20px 33px;
                text-align: center;
            }
        }
        .login-form {
            display: block;
            text-align: center;
            padding: 0 15px;
            .alert.alert-danger,
            .alert.alert-success {
                display: block;
                margin-bottom: 20px;
            }
            &__content {
                width: 100%;
            }
            &__title {
                font-weight: bold;
                font-size: 24px;
                margin-bottom: 40px;
                line-height: 130%;
                color: #fff;
            }
            .custom-selectpick .dropdown-toggle {
                height: 45px !important;
                border-color: #ececf5;
            }
            .dropdown-menu.show {
                border-color: #ececf5;
            }
            .form-check-label {
                font-size: 14px;
                margin-bottom: 30px;
                color: #fff;
                a {
                    color: #fff;
                    text-decoration: none;
                }
            }
            
            &__copyright {
                font-size: 14px;
                font-weight: normal;
                text-align: center;
                margin-bottom: 40px;
                margin-top: auto;
                span {
                    color: rgba(255, 255, 255, .50);
                    display: block;
                }
                a {
                    color: #fff;
                }
            }
        }
    }

    .form-element {
        &__input {
            width: 100%;
            height: 45px;
            border-color: #ECECF5;
            padding-left: 20px;
            padding-right: 20px;
            font-size: 13px;
        }
        &__button {
            width: 100%;
            line-height: 1.4;
            min-width: 170px;
            width: auto;
            border: none;
            &.light-purple {
                background: #8f6cff;
                border: none;
            }
        }
        .custom-selectpick {
            .btn.dropdown-toggle {
                height: 50px;
                border-radius: 25px;
                background-color: #fff;
            }
        }
        //.height: 50px;
    }

    .profile-card-place {
        margin-bottom: 30px;
        &__list {

        }
        &__item {
            padding-bottom: 17px;
            margin-bottom: 17px;
            border-bottom: 1px solid #ECECF5;
        }
        &__name {
            color: #9D9DB1;
            font-size: 14px;
            margin-bottom: 17px;
        }
        &__card-number {
            margin-left: auto;
            margin-right: 22px;
        }
        &__card-exp {
            margin-right: 20px;
        }
        &__add {
            font-size: 15px;
            color: #3B2483;
            background-image: url('../img/plus.svg');
            background-repeat: no-repeat;
            padding-left: 20px;
            background-position-y: center;
        }
    }

    .custom-rounded-input {
        height: 50px;
        border: none;
        border-radius: 25px;
        &:focus {
            border-color: #8DC899;
            box-shadow: 0 0 0 0.2rem rgba(141, 200, 153, .25);
            outline: none !important;
        }
    }

    .custom-rounded-dropdown {
        display: inline-block;
        margin-right: 17px;
        min-width: 90px;
        .dropdown-toggle {
            width: 100%;
            border: 2px solid #9D9DB1;
            box-sizing: border-box;
            border-radius: 20px;
            background-color: transparent;
            color: #9D9DB1;
            text-align: left;
            position: relative;
            outline: none !important;
            &:focus {
                box-shadow: none;
            }
        }
        &.custom-selectpick {
            .dropdown-menu {
                right: 0;
                .dropdown-item.active, 
                .dropdown-item:active {
                    background: #F7F7FB;
                }
                li {
                    .dropdown-item.disabled {
                        cursor: not-allowed;
                        color: #dcdcdc;
                    }
                }
            }
            &.show {
                .btn.dropdown-toggle {
                    border-radius: 20px 20px 0 0 !important;
                }
            }
            .dropdown-toggle {
                border-color: rgba(157,157,177,.5);
                outline: none !important;
            }
        }
        &.fix-width {
            .dropdown-toggle {
                min-width: 150px;
                padding-left: 1.5rem;
                padding-right: 1.5rem;
                &:after {
                    position: absolute;
                    right: 20px;
                    top: 17px;
                }
            }
            .dropdown-menu {
                min-width: 150px;
            }
        }
        >.dropdown-menu {
            border-top: 0;
            margin-top: -2px;
            min-width: auto;
            width: 100%;
            &.show {
                display: block;
                border: 2px solid rgba(157, 157, 177, 0.5);
                box-sizing: border-box;
                border-radius: 0 0 20px 20px;
                border-top: 0;
            }
            .dropdown-item {
                font-size: 15px;
                color: #9D9DB1;
                padding-top: 4px;
                padding-bottom: 4px;
            }
            .btn {
                
            }
        }
        &.show {
            .dropdown-toggle {
                border: 2px solid rgba(157, 157, 177, 0.5);
                box-sizing: border-box;
                border-radius: 20px 20px 0 0;
                background-color: #fff;
                color: #9d9db1;
                &:focus {
                    box-shadow: none;
                }
            }
        }
    }
    select.custom-rounded-dropdown {
        min-width: auto;
    }

    .custom-rounded-btn {
        height: 50px;
        border-radius: 43px;
        font-weight: 800;
        font-size: 15px;
        text-align: center;
        padding: .85rem .75rem;
        &.fill-width {
            width: 100%;
        }
        &.with-shadow {
            webkit-box-shadow: 0 14px 15px -12px #a991f2;
            -moz-box-shadow: 0 14px 15px -12px #a991f2;
            box-shadow: 0 14px 15px -12px #a991f2;
        }
        &:focus {
            border-color: #8DC899;
            box-shadow: 0 0 0 0.2rem rgba(141, 200, 153, .25);
        }
        &.purple {
            background: #5C3CC0;
            color: #fff;
            &:hover {
                background: #453184 !important;
            }
        }
        &.purple-line {
            border: 2px solid #5C3CC0;
            background-color: transparent;
            color: #5C3CC0;
            &:hover {
                background-color: #5C3CC0;
                color: #fff;
            }
        }
        &.light-purple {
            background: #9B8DF250;
            color: #fff;
            &:hover {
                background: #5C3CC0 !important;
            }
        }
        &.grey-line {
            color: #9d9db1;
            border: 2px solid rgba(157,157,177,.31);
            background-color: transparent;
            &:hover {
                color: #fff;
                background: #9d9db1;
            }
        }
        &.green {
            background: #60996C;
            color: #fff;
            border: none;
            &:hover {
                background: #3b6b45 !important;
            }
        }
        &.light-green {
            background: #8DC899;
            color: #fff;
            border: none;
            &:hover {
                background: #60996C;
            }
        }
        &.light-red {
            background: #f95e5e;
            color: #fff;
            border: none;
            &:hover {
                background: #d22424;
            }
        }
        &.green-line {
            border: 2px solid #8DC899;
            background-color: transparent;
            color: #8DC899;
            &:hover {
                background-color: #8DC899;
                color: #fff;
            }
        }
        &.green-line-dark {
            border: 2px solid #60996c;
            background-color: transparent;
            color: #60996c;
            &:hover {
                background-color: #60996c;
                color: #fff;
            }
        }
        &.grey-line {
            border: 2px solid #9D9DB1;
            background-color: transparent;
            color: #9D9DB1;
            &:hover {
                background-color: #9D9DB1;
                color: #fff;
            }
        }
        &:not(:disabled):not(.disabled):active:focus {
            box-shadow: 0 0 0 0.2rem rgba(141, 200, 153,.5);        
        }
        &:not(:disabled):not(.disabled):active {
            background: #453184;
        }
    }
    .header {
        height: 100px;
        background-color: #8dc899;
        display: flex;
        align-items: flex-end;
        padding: 13px 17px;
        &__toggle {
            // margin-right: auto;
            font-size: 21px;
            color: #fff;
            height: 30px;
            width: 33px;
            background-image: url('../img/hamburger.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-color: transparent;
            border: none;
        }
        &__logo {
            margin-left: auto;
            margin-right: auto;
            max-width: 128px;
            img {
                max-width: 100%;
            }
        }
        &__edit-info {
            padding: 4px;
        }
    }
    .alert {
        font-size: 11px;
        &.light-grey {
            color: #9D9DB1;
            background: #ECECF5;
            .custom-rounded-btn {
                color: #9D9DB1;
                border: 2px solid #9D9DB150;
            }
            .custom-link {
                font-size: 10px;
                color: #9D9DB1;
                text-transform: uppercase;
                text-decoration: underline;
            }
        }
    }
    .dash-user-info {
        margin-top: 25px;
        align-items: center;
        position: relative;
        display: block;
        &__head {
            display: flex;
            align-items: center;
            &-toggle {
                margin-left: auto;
                margin-bottom: auto;
                margin-top: 16px;
                color: #9D9DB1;
            }
        }
        .loader-place {
            position: absolute;
            width: 100%;
            justify-content: center;
            align-items: center;
            display: flex;
            background-color: rgba(255,255,255,.7);
            z-index: 99;
            .lds-ring {
                margin: 42px;
            }
        }
        &__image {
            width: 90px;
            height: 90px;
            margin-right: 20px;
            flex: initial;
            position: relative;
            margin-bottom: 15px;
            img {
                width: 100%;
                height: 100%;
                overflow: hidden;
                border-radius: 50%;
                object-fit: cover;
                position: absolute;
                z-index: 1;
                border: 3px solid #E1E1EA;
            }
            &.edit {
                width: 196px;
                height: 196px;
                margin-right: 0;
            }
            .user-image-manage {
                position: absolute;
                z-index: 88;
                right: 20px;
                bottom: 8px;
                width: 50px;
                height: 50px;
                .custom-file-label {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    margin-bottom: 0;
                    border-radius: 100%;
                    justify-content: center;
                    align-items: center;
                    background: url('../img/camera-line.svg') #E1E1EA;
                    background-repeat: no-repeat;
                    background-position: center;
                    cursor: pointer;
                    z-index: 4;
                    &:after {
                        display: none;
                    }
                }
            }
        }
        &__content {
            flex: 1;
            padding-bottom: 20px;
            &-company {
                font-family: Raleway;
                font-weight: bold;
                font-size: 18px;
            }
            &-user {
                font-weight: 400;
                font-size: 1rem;
                margin-bottom: 20px;
            }
            &-label {
                font-size: .789rem;
                font-weight: 600;
                color: #9d9db1;
                display: block;
                flex: 0 0 70px;
                margin-right: 10px;
            }
            &-value {
                font-weight: normal;
                font-size: 13px;
                margin-bottom: 0;
                text-align: left;
            }
        }
    }

    .user-main-info {
        padding: 0 20px;
        &__item {
            display: flex;
        }
    }
}

.light-green-color {
    color: #8dc899;
}

.light-red-color {
    color: #df8b98;
}

.light-grey-color {
    color: #9d9db1;
}

.custom-tabs {
    position: relative;
    justify-content: center;
    &__item {
        margin-right: 25px;
        margin-left: 25px;
        &--link {
            background-color: transparent !important;
            color: #9D9DB1 !important;
            border-radius: 0 !important;
            border-bottom: 2px solid transparent;
            padding: .2rem 0;
            font-size: .765rem;
            font-weight: 800;
            padding-bottom: 0;
            text-transform: uppercase;
            &.active {
                border-color: #8dc899;
                color: #292c49 !important;
            }
        }
        &--session {
            opacity: 0.5;
            border: 2px solid #9D9DB1;
            color: #9D9DB1;
            border-radius: 43px !important;
            font-size: 12px;
            padding: 4px 12px 4px 4px;
            cursor: pointer;
            &:hover {
                text-decoration: none;
            }
        }
        &.list {
            margin-right: 0;
            position: absolute;
            right: -18px;
        }
    }
}

.custom-card {
    border-radius: 12px;
    border: none;
    &__body {
        .custom-card-title {
            font-size: 1.2rem;
            font-weight: 800;
            margin: 0 0 15px;
        }
        &-label {
            font-size: 12px;
            color: #8c8c8c;
        }
    }
    &__empty {
        text-align: center;
        padding: 5px;
        img {

        }
        &-text {
            font-size: 14px;
            color: #9d9db1;
            display: block;
            margin-top: 8px;
        }
    }
}

.news-recent-list {
    padding: 0;
    list-style: none;
    margin-bottom: 0;
    &__item {
        a {
            display: block;
            color: initial;
            &:hover {
                text-decoration: none;
                color: #9B8DF2;
            }
        }
        padding-bottom: 10px;
        margin-bottom: 20px;
        border-bottom: 1px solid #ECECF5;
        &-date {
            color: #9D9DB1;
            font-weight: 300;
            font-size: 11px;
        }
        &-title {
