.overlay {
    display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    z-index: 998;
    opacity: 0;
    transition: all 0.5s ease-in-out;
}

.overlay.active {
    display: block;
    opacity: 1;
}

.custom-check {
    position: relative;
    cursor: pointer;
    color: #fff;
    text-align: left;
    font-size: 13px;
    input {
        opacity: 0;
        position: absolute;
        z-index: -1;
        padding: 0;
        overflow: visible;
    }
    > i {
        width: 18px;
        height: 18px;
        line-height: 1;
        border: 2px solid #fff;
        display: inline-block;
        vertical-align: middle;
        background-clip: padding-box;
        position: relative;
        left: 0;
        margin-right: .3rem;
        &:before {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            width: 0;
            height: 0;
            background-color: transparent;
            border-radius: 1px;
        }
    }
    input[type=checkbox]:checked+i:before {
        transform: translate(-50%,-65%) rotate(-45deg);
        background-color: transparent;
        width: 8px;
        height: 5px;
        border-width: 0 0 2px 2px;
        border-color: #fff;
        border-style: solid;
    }
    input[type=radio]+i {
        border-radius: 50%;
    }

    input[type=radio]+i:before {
        border-radius: 50%;
    }

    input:checked+i:before {
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%) rotate(-45deg);
        width: 10px;
        height: 10px;
        background-color: #fff;
    }
    a {
        text-decoration: none;
    }
}

.was-validated .form-control:invalid {
    border: 2px solid #dc3545 !important;
}

.was-validated .bootstrap-select select:invalid+.dropdown-toggle {
    border: 2px solid #dc3545 !important;
}

.error {
    color:red !important;
    display: block;
}
.error-text {
    border: 1px solid red !important;
}
.was-validated .form-control:valid,
.was-validated .custom-select:valid {
    background-image: none;
    padding-right: .75rem;
}
.was-validated .form-control:invalid,
.was-validated .custom-select:invalid {
    background-image: none;
    padding-right: .75rem;
}

.was-validated .form-check-input:invalid~i {
    border-color: #dc3545;
}